import { compose, createStore } from 'redux';
import { appReducer } from './reducers/appReducer';
import { appConfig } from './config/appConfig';
import { loadState } from './helpers/LocalStorageHelper';
var initialState = loadState();
export var configureStore = function () {
    var store = createStore(appReducer, initialState, compose(
    //applyMiddleware(reduxLogger),
    window[('devToolsExtension')] ? window[('devToolsExtension')]() : function (f) { return f; }));
    var dispatchAction = function (action) {
        store.dispatch(action);
    };
    dispatchAction(appConfig.Actions.SetCallback(dispatchAction));
    return store;
};



